// stylelint-disable at-rule-empty-line-before

@import "variables";
@import "functions";
@import "mixins";
@import "sprites";
@import "fonts";
@import "vendor";
@import "base";
@import "helpers";

.main {
    width: 662px;
    margin: 0 auto;
    padding: 0 22px;
    font-family: $font-family-roboto;
    font-weight: 500;
    .contacts-info {
        padding: 30px 0;
        display: flex;
        align-items: flex-start;
        position: relative;
        border-bottom: 2px solid #f0f0f0;
        .photo {
            margin-right: 23px;
        }
        .fields {
            display: flex;
            flex: 1;
            flex-direction: column;
            input {
                width: 100%;
                border: none;
                border-bottom: 2px solid #333;
                text-transform: uppercase;
                padding: 0 0 8px 0;
                margin-bottom: 33px;
                font-size: 12px;
                color: #666;
                ::placeholder {
                    text-transform: uppercase;
                    font-size: 12px;
                    color: #666;
                }
                &:focus {
                    outline: none;
                }
                &:last-of-type {
                    margin-bottom: 0;
                }
            }
        }
        &::after {
            content: url(../images/approved.png);
            width: 184px;
            height: 186px;
            position: absolute;
            bottom: -10px;
            right: -15px;
            opacity: 0;
            transform: scale(2);
            transition: opacity .2s, transform .2s;
        }
        &.normal::after {
            opacity: 1;
            transform: scale(1);
        }
    }
    .skills {
        padding: 18px 0;
        h2 {
            font-weight: 500;
            font-size: 18px;
            margin-bottom: 23px;
        }
        .skills-wrapper {
            display: flex;
            flex-wrap: wrap;
            border-bottom: 2px solid #f0f0f0;
            label {
                flex: 0 0 25%;
                margin-bottom: 40px;
                display: flex;
                align-items: center;
                font-size: 12px;
                color: #666;
                text-transform: uppercase;
                input {
                    display: none;
                    &:checked + span {
                        background: url(../images/checked-bg.png) no-repeat center #0093d7;
                        background-size: 70%;
                    }
                }
                span {
                    display: inline-block;
                    margin-right: 8px;
                    width: 19px;
                    height: 19px;
                    border-radius: 5px;
                    background: #eee;
                    transition: .2s;
                }
            }
        }
    }
    .skilljs {
        display: flex;
        align-items: flex-start;
        padding-top: 40px;
        padding-bottom: 40px;
        .diagram {
            margin-right: 70px;
            width: 254px;
            min-width: 254px;
            height: 133px;
            position: relative;
            overflow: hidden;
            position: relative;
            .colors {
                width: 254px;
                min-width: 254px;
                height: 254px;
                position: absolute;
                top: 0;
                left: 0;
                background-image: linear-gradient(45deg, #ffc815 50%, transparent 50%),
                                  linear-gradient(55deg, #fff 45%, transparent 0),
                                  linear-gradient(45deg, #fff 52%, transparent 50%),
                                  linear-gradient(135deg, #a3cd3b 50%, transparent 0),
                                  linear-gradient(135deg, #fff 52%, transparent 0),
                                  linear-gradient(140deg, #fff 0%, transparent 0),
                                  linear-gradient(180deg, #0093d7 53%, transparent 0);
                border-radius: 50%;
            }
            .pointer {
                position: absolute;
                background-color: #fff;
                border-radius: 50%;
                width: 146px;
                height: 146px;
                bottom: -73px;
                left: 0;
                right: 0;
                margin: 0 auto;
                transform: scale(.85);
                img {
                    position: absolute;
                    top: calc(50% - 20px);
                    right: calc(50%);
                    transform-origin: 100% 50%;
                }
            }
        }
        .numeric {
            h2 {
                font-size: 18px;
                line-height: 24px;
                margin-top: 0;
            }
            .value {
                font-weight: 900;
                font-size: 50px;
                color: #b0dcef;
                &.yellow {
                    color: #ffc815;
                }
                &.green {
                    color: #a3cd3b;
                }
                &.blue {
                    color: #0093d7;
                }
            }
        }
    }
}

@media only screen and (max-width: 662px) {
    .main {
        width: 100%;
        overflow: hidden;
    }
}

@media only screen and (max-width: 500px) {
    .main .contacts-info {
        flex-direction: column;
        .photo, .fields {
            width: 100%;
        }
    }
    .main .contacts-info .photo {
        margin-right: 0;
        text-align: center;
    }
    .main .skills .skills-wrapper {
        flex-wrap: wrap;
        label {
            flex: 0 0 33%;
        }
    }
    .main .skilljs {
        flex-wrap: wrap;
        justify-content: center;
        .diagram, .numeric {
            margin-bottom: 15px;
        }
        .diagram {
            margin-right: 0;
        }
        .numeric {
            text-align: center;
             h2 br {
                display: none;
            }
        }
    }
}

@media only screen and (max-width: 420px) {
    .main .skills .skills-wrapper label {
        flex: 0 0 50%;
    }
}
